
        #splashscreen {
          position: fixed;
          z-index: 1000;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          background: black;
          color: white;
          display: flex;
          flex-direction: column;
          align-items: center;

          opacity: 0;
          /* transform: scale(0); */

          animation-name: splashin;
          animation-duration: 0.4s;
          animation-delay: 0.1s;
          /* in case browser is very fast, don't display at all */
          animation-fill-mode: forwards;
        }

        #splashscreen[data-step="loading"] + #app {
          visibility: hidden;
          /*
          avoid big image to create a scroll
          or if the game is too big it will
          increase splashscreen size
          */
          width: 100%;
          height: 100%;
          overflow: hidden;
        }

        #splashscreen[data-step="loaded"] {
          animation-name: splashout;
          animation-fill-mode: forwards;
        }

        @keyframes splashin {
          from {
            opacity: 0;
            /* transform: scale(0); */
          }

          to {
            opacity: 1;
            /* transform: scale(1); */
          }
        }

        @keyframes splashout {
          from {
            opacity: 1;
          }

          to {
            opacity: 0;
            display: none;
          }
        }

        #splashscreen #splashscreen-text {
          display: flex;
          justify-content: center;
          flex: 1;
          width: 80%;
        }

        #splashscreen #errorscreen {
          display: block;
        }

        #splashscreen #errorscreen details {
          max-width: 500px;
        }

        #splashscreen details pre {
          overflow: auto;
        }

        #splashscreen img {
          animation: loading 1.3s ease-in-out infinite alternate;
        }

        @keyframes loading {
          0% {
            transform: rotate(-90deg) scale(1);
          }

          100% {
            transform: rotate(90deg) scale(1.3);
          }
        }
      